"use server";

import { cookies } from 'next/headers';
import fs from 'fs';
import path from 'path';

const USERS_DB_PATH = path.join(process.cwd(), 'data', 'users.json');

const getUsers = (): any[] => {
    if (!fs.existsSync(USERS_DB_PATH)) return [];
    try {
        const data = fs.readFileSync(USERS_DB_PATH, 'utf-8');
        return JSON.parse(data);
    } catch {
        return [];
    }
}

export async function loginAction(formData: FormData) {
    const email = formData.get('email') as string;
    const password = formData.get('password') as string;

    if (!email) {
        return { success: false, message: '„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ' };
    }

    const users = getUsers();
    const user = users.find(u => u.email === email);

    if (!user) {
        return { success: false, message: '„É¶„Éº„Ç∂„Éº„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„ÄÇ„Ç¢„Ç´„Ç¶„É≥„ÉàÁôªÈå≤„ÅØ„ÅäÊ∏à„Åø„Åß„Åô„ÅãÔºü' };
    }

    // Verify password
    if (!user.password) {
        return { success: false, message: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ„Äå„Éë„Çπ„ÉØ„Éº„Éâ„Çí„ÅäÂøò„Çå„Åß„Åô„ÅãÔºü„Äç„Åã„ÇâË®≠ÂÆö„ÇíË°å„Å£„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ' };
    }

    if (user.password !== password) {
        return { success: false, message: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÈñìÈÅï„Å£„Å¶„ÅÑ„Åæ„Åô„ÄÇ' };
    }
    // If user has no password (old users), we might want to allow them or force reset.
    // For now, we allow them if they didn't input a password? Or just allow them regardless for backward compatibility?
    // Let's rely on client side: if client sends password, we check it.
    // If client sends password but user has none? Then we might allow or fail.
    // Let's say: if user.password is set, check it.

    // Set cookie
    // In a real app, use a secure session token. Here we strictly simulate by storing userId.
    const cookieStore = await cookies();
    cookieStore.set('userId', user.id, { httpOnly: true, path: '/' });
    cookieStore.set('userName', user.name, { httpOnly: false, path: '/' }); // For client usage easily

    return { success: true, message: '„É≠„Ç∞„Ç§„É≥„Åó„Åæ„Åó„Åü„ÄÇ' };
}

export async function logoutAction() {
    const cookieStore = await cookies();
    cookieStore.delete('userId');
    cookieStore.delete('userName');
    return { success: true };
}

export async function getCurrentUser() {
    const cookieStore = await cookies();
    const userId = cookieStore.get('userId')?.value;
    if (!userId) return null;

    const users = getUsers();
    return users.find(u => u.id === userId) || null;
}
